import React from 'react';
import { PageLoading, Notification } from '@/components';
const notice = Notification.notice;

/**
 *  
 */
export default {
  // 
  request: {
    prefix: '/api',

    /**
     * modelEnhance
     * 
     * {status: true, data: ...} // 
     * {status: false, message: ...} // 
     * 
     * response
     * 
     */
    afterResponse: (response) => {
      const {status, data, message} = response;
      if (status) {
        return data;
      } else {
        notice(message, 'error');
        return false;
      }
    },
    errorHandle: (err) => {
      // 
      if (err.name === 'RequestError') {
        notice(err.text || err.message, 'error');
      }
    }
  },

  // 
  exception: {
    global: (err, dispatch) => {
      const errName = err.name;
      // RequestError
      if (errName === 'RequestError') {
        console.error(err); 
      } else {
        console.error(err);
      }
      notice(err.message, 'error');
    },
  },

  // 
  pageHelper: {
    // 
    requestFormat: (pageInfo) => {
      const { pageNum, pageSize, filters, sorts } = pageInfo;
      return {
        currentPage: pageNum,
        showCount: pageSize,
        sortMap: sorts,
        paramMap: filters,
      }
    },

    // 
    responseFormat: (resp) => {
      const { currentPage, showCount, totalResult, dataList, totalPage } = resp;
      return {
        pageNum: currentPage,
        pageSize: showCount,
        total: totalResult, 
        totalPages: totalPage,
        list: dataList
      }
    }
  },

  // 
  router: {
    loading: <PageLoading loading />
  },
  
  /**
   * 
   * 
   * 
   * {
   *   status: true,
   *   data: responseData
   * }
   * 
   * {
   *   status: false,
   *   code: 500,
   *   message: ''
   * }
   *  orz...
   */
  mock: {
    toSuccess: (response) => ({
      status: true,
      data: response,
    }),

    toError: (message) => ({
      status: false,
      message: message,
    }),
  },

  /**
   * , modelEnhance.js,
   */
  notice: {
    success: (message) => notice(message, 'success'),
    error: (message) => notice(message, 'error'),
    warning: (message) => notice(message, 'warn'),
    info: (message) => notice(message),
  }
}


